import React from 'react';
import styled from "styled-components";
import "../App.css";
import picture from "../images/main_picture_3.png";
import Fade from "react-reveal/Fade";

const StyledImg = styled.img`
    width: 100%;
    padding-bottom:0;
    display:block;
`;
const StyledContainer = styled.div`
    background-color: #fbd7d0;
    margin-top: 0;
`;
const Contents = styled.div`
    font-size: 90%;
    text-align: center;
    color: #424242;
    font-family: "MaruBuri";
    font-weight: 100;
    padding-bottom: 20%;
    padding-top: 2%;
`;

export default function TopContainer2() {
    return (
        <div >
            <StyledImg src={picture}/>
            <StyledContainer>
                <Fade bottom>
                    <Contents>
                        <div>2022년 9월 18일 일요일 오후 3시</div>
                        <div>소노펠리체 컨벤션 다이아몬드홀 (단독홀)</div>
                    </Contents>
                </Fade>
            </StyledContainer>
        </div>
    )
        ;
}