import React from 'react';
import MapComponent from "../components/MapComponent";
import styled from "styled-components";

const StyledContainer = styled.div`
    align-items: center;
    text-align: center;
`;


export default function MapContainer() {
    return (
        <StyledContainer>
            찾아오시는 길
            <MapComponent/>
        </StyledContainer>
    );
}